@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.Card {
    display: flex;
    align-items: center;
    width: 729px;
    height: 310px;
    display: flex;
    background-color: #383A45;
    border-radius: 10px;
    font-family: 'Poppins', sans-serif;
}

.Card_Image img {
    width: 262px;
    height: 250px;
    object-fit: cover;
    padding: 0 30px;

}

.Card_Body {
    padding-right: 43px;
}

.Card_Title {
    font-size: 1rem;
    font-weight: 400;
}

.Card_Description {
    color: #8D91A6;

    line-height: 25px;
    font-size: 1rem;
    margin-bottom: 20px;
}